h1 {
    color: #747474;
    font-family: "Almarai", sans-serif;
    font-size: 56px;
    font-weight: 800;
}

.aboutBody h1 {
    color: white;
    font-family: "Almarai", sans-serif;
    font-size: 56px;
    font-weight: 800;
    line-height: 40px;
}

h2 {
    color: #747474;
    font-family: "Almarai", sans-serif;
    font-size: 40px;
    font-weight: 500;
}

.aboutBody h2 {
    color: white;
    font-family: "Almarai", sans-serif;
    font-size: 40px;
    font-weight: 500;
}

p {
    color: #747474;
    font-family: "Almarai", sans-serif;
    font-size: 16px;
    justify-content: center;
    font-weight: 300;
    line-height: 20px;
}

.aboutBio p {
    color: white;
    font-family: "Almarai", sans-serif;
    font-size: 20px;
    font-weight: 300;
    line-height: 24px;
    margin-top: 10px;
}

.aboutInfo p {
    color: white;
    font-family: "Almarai", sans-serif;
    font-size: 20px;
    font-weight: 300;
    line-height: 20px;
}

small {
    font-size: 12px;
}

.aboutBio {
    margin-top: 20px;
    display: flex;
    margin-bottom: 40px;
}

.aboutBioContainer {
    background-color: #F8C5C5;
    display: flex;
    align-items: center;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-right: 20px;
}

.aboutBioGrid {
    display: grid;
    grid-template-columns: 80px 8fr 20px 4fr;
    margin-right: 80px;
}

.aboutBioPicture {
    width: 90%;
    height: auto;
}

.aboutBioPictureContainer {
    background-color: #F8C5C5;
    padding-bottom: -10px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.aboutBioSidepiece {
    background-color: #F8C5C5;
}

.aboutGridGap {
    background: white;
}

.aboutGlance {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2vh;
}

.aboutHobbies {
    display: flex;
    margin-bottom: 40px;
}

.aboutHobbiesDot {
    color: white;
    margin-left: 8px;
    margin-right: 8px;
}

.aboutHobbiesGrid {
    display: grid;
    grid-template-columns: 80px 4fr 20px 4fr 20px 4fr 80px;
}

.aboutHobbiesHeadingContainer {
    background-color: #67A2E8;
    display: flex;
    align-items: center;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-right: 20px;
    justify-content: center;
}

.aboutHobbiesHobby {
    color: white;
    font-size: 20px;
    line-height: 28px;
}

.aboutHobbiesHobby:hover {
    font-size: 24px;
    font-weight: bold;
    transition: ease-in 0.3s;
}

.aboutHobbiesIcon {
    width: 32px;
    height: 32px;
    margin-left: 8px;
    margin-right: 8px;
    margin-top: 16px;
    margin-bottom: 16px;
    color: white;
}

.aboutHobbiesIcon:hover {
    width: 40px;
    height: 40px;
    margin-left: 4px;
    margin-right: 4px;
    margin-top: 12px;
    margin-bottom: 12px;
    color: #F8C5C5;
    transition: ease-in-out 0.3s;
}

.aboutHobbiesSidepiece {
    background-color: #67A2E8;
}

.aboutHobbiesImageTop {
    width: 100%;
    height: auto;
}

.aboutHobbiesImageTopGrid {
    display: grid;
    grid-template-columns: 2fr 20px 2fr;
}

.aboutHobbiesImageBottom {
    width: 100%;
    height: auto;
    margin-top: 20px;
}

.aboutIcons {
    height: 32px;
    width: 32px;
    margin-right: 8px;
    min-height: 32px;
    min-width: 32px;
}

.aboutIconsSmall {
    font-size: 14px;
    margin-left: 4px;
    margin-right: 8px;
    color: white;
    min-width: 14px;
    min-width: 14px;
}

.aboutInfo {
    display: flex;
    margin-bottom: 40px;
}

.aboutInfoContainer { 
    background-color: #A793D1;
    display: flex;
    align-items: center;
    padding: 40px;
}

.aboutInfoContainerGrid {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.aboutInfoGrid {
    display: grid;
    grid-template-columns: 80px 9fr 20px 3fr 80px;
}

.aboutInfoHeadingContainer {
    background-color: #A793D1;
    display: flex;
    align-items: center;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 20px;
    justify-content: center;
}

.aboutInfoSidepiece {
    background-color: #A793D1;
}

.aboutPhotos {
    width: 100%;
    height: auto;
}

.button {
    border: solid 1px;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 12px;
    padding-bottom: 12px;
    width: fit-content;
    text-decoration: none;
}

.button:hover {
    color: white;
    background-image: linear-gradient(90deg, #F8C5C5, #A793D1, #67A2E8);
    border-color: #747474;
    transition: ease-in-out 0.2s;
}

.container {
    padding-left: 80px;
    padding-right: 80px;
}

.homeBody {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 80%;
}

.homeContainer {
    background-image: url('../imgs/home-bg.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    height: 100%;
    padding-left: 80px;
    padding-right: 80px;
}

.homeIcon {
    color: #747474;
    margin-right: 8px;
    font-size: 24px;
}

.horizontalLine {
    height: 1px;
    width: 40%;
    max-width: auto;
    border: none;
    background-color: white;
}

.fill-window {
    height: 100%;
    position: absolute;
    left: 0;
    width: 100%;
    overflow: hidden;
}

.flexContainer {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.flexContainerColumn {
    display: flex;
    flex-direction: column;
}

.fontBody {
    font-family: "Almarai", sans-serif;
    letter-spacing: 1.5px;
}

.fontBold {
    font-weight: bold;
}

.fontButton {
    font-family: "Anaheim", sans-serif;
    letter-spacing: 1.5px;
}

.fontCenterJustify {
    justify-content: center;
}

.fontCenterAlign {
    align-items: center;
}

.fontColorGray {
    color: #747474
}

.fontSize16 {
    font-size: 16px;
}

.fontSize20 {
    font-size: 20px;
}

.fontSize24 {
    font-size: 24px;
}

.fontUnderline {
    text-decoration: underline;
}

.fontUnderline:hover {
    color: black;
    transition: ease-in 0.3s;
}

.fontWrap {
    flex-wrap: wrap;
}

.marginBottom10 {
    margin-bottom: 10px;
}

.marginBottom20 {
    margin-bottom: 20px;
}

.marginTop50 {
    margin-top: 50px;
}

.nameLogo {
    width: 25vh;
    height: auto;
}

.socialIcons {
    width: 32px;
    height: 32px;
    margin-left: 8px;
    margin-right: 8px;
    margin-top: 10px;
    margin-bottom: 10px;
    color: white;
}

.socialIcons:hover {
    width: 48px;
    height: 48px;
    margin-left: 0px;
    margin-right: 0px;
    margin-top: 2px;
    margin-bottom: 2px;
    transition: ease-in-out 0.3s;
}