.menuCurrentlyAt {
    font-weight: bold;
    border: solid 1px;
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 8px;
    padding-bottom: 8px;
}

.menuCurrentlyAtContact {
    font-weight: bold;
    border: solid 1px;
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 8px;
    padding-bottom: 8px;
    margin-right: -16px;
}

.menuContainer {
    justify-content: flex-end;
}

.menuIcon {
    display: none;
}

.menuLinks {
    color: #747474;
    text-decoration: none;
    margin-left: 40px;
}

.menuLinks:hover {
    color: black;
    padding-bottom: 8px;
    border-bottom: solid 1px;
    transition: all 0.3s ease-out;
}

.menuLinksWhite {
    color: #E4E4E4;
    text-decoration: none;
    margin-left: 40px;
}

.menuLinksWhite:hover {
    color: white;
    padding-bottom: 8px;
    border-bottom: solid 1px;
    transition: all 0.3s ease-out;
}

.navbarContainer {
    background-color: transparent;
    display: flex;
    align-items: center;
    font-family: 'Anaheim';
    color: #747474;
    font-size: 24px;
    /*filter: drop-shadow(0px 10px 4px rgba(0,0,0,0.1));*/
    justify-content: space-between;
    padding-top: 10px;
    padding-bottom: 10px;
    letter-spacing: 2px;
}

.navbarLogoContainer {
    display: flex;
    align-items: center;
}

.navbarName {
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    color: #747474;
    letter-spacing: 2px;
}

.navbarName:hover {
    color: black;
    transition: all 0.5s ease-out;
}

.navbarLogo {
    height: 4rem;
    width: auto;
}

.navMenu {
    display: flex;
    list-style: none;
}